@use '../utils/mixins.scss' as *;

.section {
  &__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    letter-spacing: 0px;
    margin-bottom: 48px;

    @include onTablet {
      margin-bottom: 56px;
      font-size: 40px;
      line-height: 40px;
    }

    @include onDesktop {
      font-size: 48px;
      line-height: 48px;
    }
  }
}
